h2 {
    margin-bottom: 2rem;
}

.page-container {
    margin-top: 10rem;
    margin-left: 15%;
}

.about {
    margin: 1rem;
}

.aboutIcon {
    margin: 1rem;
}

.aboutText {
    margin: 1rem;
}

.contact-container {
    margin: 5rem;
    display: flex;
    justify-content: center;
}

.thanks {
    display: block;
    margin-left: 45%;
    margin-top: 10%;
}

.form-container {
    display: flex;
    align-items: center;
    margin-top: 3rem;
    padding: 3rem;
    text-align: center;
}

.icon-container {
    margin: 1rem;
}

.btn {
    color: white;
    font-weight: bold;
    text-transform: capitalize;
    border-radius: 5px;
    /* text-white active:bg-info font-bold uppercase text-sm py-1 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150 */
}

.btn:hover , .btn:focus, .btn:active, .btn.active {
    background-color: aqua;
    transition: .25s;
}

 @media (max-width: 768px) {

    .page-container {
        margin: 2rem;
    }

    .contact-container {
        margin: 0;
    }

    .thanks {
        margin-left: 35%;
    
    }

 }